<template>
  <transition name="modal-fade">
    <div class="vue-dynamic-modal" v-if="dynamicModals.length" @click.self="handleWrapperClick">
      <div class="modal-container-box" v-for="modal in dynamicModals" :key="modal.id" v-bind="modal.config">
        <div class="mask" :style="{zIndex: 2000 + uid}"></div>
        <div class="modal-wrapper" :style="{zIndex: 2000 + uid}">
          <div :class="['modal-container', modal.config.customClass]" :style="calcStyle(modal.config)">
            <div class="modal-header">
              {{ modal.config.modalTitle }}
              <span class="modal-header-close" @click="remove(modal.id)"></span>
            </div>
            <div class="modal-content">
              <component :is="modal.component" v-bind="modal.contentConfig" @close="close(modal.id, $event)" @cancel="remove(modal.id)" @off="off(modal.id, $event)">
              </component>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script lang='ts'>
import { defineComponent, shallowRef } from 'vue'
import cloneDeep from 'lodash/cloneDeep'
import { modalService } from './index'

export default defineComponent({
  name: 'modal',
  props: {},
  computed: {},
  data(): any {
    return {
      uid: 0,
      dynamicModals: [],
    }
  },
  created() {
    modalService._setDynamicContainer(this)
  },
  methods: {
    calcStyle(config: any = {}) {
      let style: any = {
        marginTop: '15vh',
      }
      if (config.width) {
        style.width = config.width
      }
      return style
    },
    add(modal: any, config: any = {}, promise: Promise<any>) {
      const id = this.uid++
      if (modal) {
        config.modalTitle = config.title
        delete config.title
        const contentConfig = cloneDeep(config)
        // delete contentConfig.title
        const modalDefaultOptions = {
          // fullscreen:
          // zIndex: 1025
        }
        config = {
          ...modalDefaultOptions,
          ...config,
        }
        this.dynamicModals.push({
          id: id,
          visible: true,
          config: config,
          contentConfig: contentConfig,
          component: shallowRef(modal),
          promise: promise,
        })
      }
    },
    remove(id: number) {
      for (const i in this.dynamicModals) {
        if (this.dynamicModals[i].id === id) {
          this.dynamicModals.splice(i, 1)
          return
        }
      }
    },
    close(id: number, $event: EventTarget) {
      /**  close Promise, modal, close */
      for (const i in this.dynamicModals) {
        if (this.dynamicModals[i].id === id) {
          const { resolve } = this.dynamicModals[i].promise
          const reParams = cloneDeep($event)
          resolve({
            data: reParams,
            props: this.dynamicModals[i].contentConfig,
            close: () => {
              this.dynamicModals.splice(i, 1)
            },
          })
          return
        }
      }
    },
    off(id: number, $event: EventTarget) {
      /** modal  */
      for (const i in this.dynamicModals) {
        if (this.dynamicModals[i].id === id) {
          const { resolve } = this.dynamicModals[i].promise
          const reParams = cloneDeep($event)
          this.dynamicModals.splice(i, 1)
          resolve(reParams)
        }
      }
    },
  },
})
</script>

<style></style>
